import React, { useState, useEffect } from 'react'

import { Card, Typography, Row, Col, Button } from 'antd'
import { HeartOutlined } from '@ant-design/icons'

const { Title, Paragraph } = Typography

const destinations = [
  { name: '巴厘岛', price: '¥3999', desc: '浪漫热带风情', imageKey: 'beach' },
  {
    name: '马尔代夫',
    price: '¥9999',
    desc: '水上木屋体验',
    imageKey: 'maldives'
  },
  { name: '东京', price: '¥5999', desc: '现代与传统交融', imageKey: 'tokyo' }
]

const TravelPriceList = () => {
  const renderCards = () =>
    destinations.map((destination, index) => {
      const imageUrl = `https://source.unsplash.com/300x200/?${destination.imageKey},${index}`
      return (
        <Col key={index} className="w-full sm:w-1/2 lg:w-1/3 p-4">
          <Card
            hoverable
            cover={<img src={imageUrl} />}
            actions={[
              <Button type="primary" className="rounded-lg">
                立即预订
              </Button>,
              <HeartOutlined key="like" />
            ]}
            className="rounded-lg overflow-hidden shadow-md"
          >
            <Title level={4}>{destination.name}</Title>
            <Paragraph className="text-lg">{destination.price}</Paragraph>
            <Paragraph className="text-sm text-gray-600">
              {destination.desc}
            </Paragraph>
          </Card>
        </Col>
      )
    })

  return (
    <div className="container mx-auto px-4 py-8">
      <Title level={2} className="text-center">
        精选旅行目的地
      </Title>
      <Paragraph className="text-center mb-8">
        发现新鲜、独特的旅行体验
      </Paragraph>
      <Row justify="center">{renderCards()}</Row>
    </div>
  )
}

export default TravelPriceList
