import React, { useState, useEffect } from 'react'

import { Card, Rate } from 'antd'
import 'antd/dist/antd.css' // 添加Ant Design样式文件
// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
// import { faStar } from '@fortawesome/free-solid-svg-icons'

const TravelAgencyPricing = () => {
  // 随机数生成函数，用于图片URLs
  const randomNum = () => Math.floor(Math.random() * 50)

  // 自定义Hook，用于生成不重复的关键词
  const useRandomKeywords = () => {
    const keywords = [
      'travel',
      'adventure',
      'vacation',
      'holiday',
      'explore',
      'tour'
    ]
    return keywords.map((keyword) => `${keyword},${randomNum()}`).join(',')
  }

  // 价格卡片的数据
  const pricingPlans = [
    {
      title: '经济舒适游',
      price: '￥2,999',
      description: '适合短途旅行和节省预算的旅客。',
      features: [
        { key: '1', text: '国内航班' },
        { key: '2', text: '三星级酒店住宿' },
        { key: '3', text: '包含早餐' },
        { key: '4', text: '市内观光游', included: false },
        { key: '5', text: '24小时客服支持' }
      ],
      imageUrl: `https://source.unsplash.com/300x200/?economy,${useRandomKeywords()}`,
      rating: 3
    },
    {
      title: '豪华旅行团',
      price: '￥5,999',
      description: '全方位服务，舒适豪华的旅行体验。',
      features: [
        { key: '1', text: '国际航班' },
        { key: '2', text: '五星级酒店住宿' },
        { key: '3', text: '全包吃喝' },
        { key: '4', text: '私人导游' },
        { key: '5', text: '包车服务' },
        { key: '6', text: '旅游保险' }
      ],
      imageUrl: `https://source.unsplash.com/300x200/?luxury,${useRandomKeywords()}`,
      rating: 5
    },
    {
      title: '探索之旅',
      price: '￥4,499',
      description: '探索未知，享受旅行的每一刻。',
      features: [
        { key: '1', text: '多地直飞' },
        { key: '2', text: '特色民宿体验' },
        { key: '3', text: '地道美食推荐' },
        { key: '4', text: '深度文化体验' },
        { key: '5', text: '摄影指南服务', included: false }
      ],
      imageUrl: `https://source.unsplash.com/300x200/?explore,${useRandomKeywords()}`,
      rating: 4
    }
  ]

  return (
    <div className="container mx-auto mt-10">
      <div className="grid grid-cols-1 sm:grid-cols-3 gap-6">
        {pricingPlans.map((plan) => (
          <Card
            hoverable
            className="rounded-xl overflow-hidden shadow-lg"
            cover={<img alt="" src={plan.imageUrl} />}
            key={plan.title}
          >
            <Card.Meta
              title={
                <div className="font-semibold text-lg text-gray-800">
                  {plan.title}
                </div>
              }
              description={
                <div className="text-sm text-gray-600">{plan.description}</div>
              }
            />
            <div className="py-4">
              <div className="text-2xl font-bold text-indigo-600">
                {plan.price}
              </div>
              <Rate
                disabled
                defaultValue={plan.rating}
                className="text-sm text-yellow-400"
              />
            </div>
            <ul>
              {plan.features.map((feature) => (
                <li
                  key={feature.key}
                  className={`flex items-center space-x-2 ${
                    feature.included === false
                      ? 'text-gray-400 line-through'
                      : 'text-gray-800'
                  }`}
                >
                  {/* {feature.included !== false && (
                    <FontAwesomeIcon
                      icon={faStar}
                      className="text-yellow-400"
                    />
                  )} */}
                  <span>{feature.text}</span>
                </li>
              ))}
            </ul>
          </Card>
        ))}
      </div>
    </div>
  )
}

export default TravelAgencyPricing
